<template>
  <div class="add">
       <div class='head'>
           <router-link to="/edit" tag="span">
                 <van-icon name="arrow-left"  @click="tui()"/>
           </router-link>
           <span>新增地址</span>
       </div>
       <div class="main">
           <div class="more">
               <div class="in">
                   <input type="text" placeholder="   请填写你的姓名">
               </div>
               <div class="in">
                   <input type="text" placeholder="   小区/写字楼/学校等">
               </div>
               <div class="in">
                   <input type="text" placeholder="   请填写详细送餐地址">
               </div> 
               <div class="in">
                   <input type="text" placeholder="   请填写能够联系到您的手机号">
               </div>
               <div class="in">
                   <input type="text" placeholder="   备用联系电话（选填）">
               </div> 
            </div> 
            <div class="btn">
               <button class="out">新增地址</button>
            </div>          
       </div>
  </div>
</template>
<script>
export default {
	components: {
	},
  methods:{
	  tui() {
		  this.$router.go(-1)
	  }
  }
}
</script>
<style lang="scss" scoped>
.add{
   height:100%;
   display: flex;
   flex-direction: column;
}
.head{
    height:1.4rem;
    background: #3190e8;
    color: white;
    display: flex;
    justify-content: space-between;
	align-items: center;
    font-weight: 600;
	span:nth-of-type(1){
		margin-left: 0.5rem;
		font-size: 0.45rem;
	}
	span:nth-of-type(2){
		margin-right: 4rem;
		font-size: 0.45rem;
	}
}
.main{
	margin-top: 0.5rem;
}
.in{
	width: 100%;
    height:1.3rem;
    background: white;
    text-align: center;
}

.in>input{
     width: 95%;
      height: 1rem;
     border: none;
     background: #f2f2f2;
     border: 1px solid #ddd;
     border-radius: 0.15rem;
     font-size: 0.45rem;
}

.btn{
     width:100%;
      display: flex;
      justify-content: space-around;
      margin-top: 0.5rem;
}
.out{
      width: 96%;
      height:1rem;
      border: none;
      background: #4cd964;
      color: white;
      font-size: 0.45rem;
      border-radius: 0.15rem;
      cursor: pointer;
	  font-size: 0.45rem;
}
</style>



